import { Injectable } from '@angular/core';
import { Skill } from 'src/Classes/Skill';
import { ISkillService } from './ISkillService';
import { environment } from 'src/environments/environment.prod';
import { BehaviorSubject } from 'rxjs';
import { AngularFirestore } from '@angular/fire/firestore';
import { Status } from 'src/Classes/Status';

@Injectable({
  providedIn: 'root'
})

export class SkillFirebaseService extends ISkillService {
  COLLECTION = environment.COLLECTIONS.SKILL;

  private skills = new BehaviorSubject([]);

  constructor(private afStore: AngularFirestore) {
    super();
    this.registerSkillsChanges();
  }

  // Private Methods
  private registerSkillsChanges() {
    this.afStore.collection(this.COLLECTION)
      .ref.where('Status', '==', Status.AVAILABLE)
      .get().then(snap => {
        this.skills.next(
          snap.docs.map(doc => {
            let tmp = doc.data();
            tmp.Id = doc.id;
            return tmp as Skill;
          })
        )
      })
  }
  // Public Methods
  public get Skills(): any {
    return this.skills.asObservable();
  }

  public Get(id?: string): Skill | Skill[] {
    if (id == null || id === undefined) {
      return this.skills.value;
    } else {
      return this.skills.value.filter(skill => skill.Id === id)[0];
    }
  }

  public Update(_item: Skill): boolean | Skill | Promise<any> {
    const Id = _item.Id;
    delete _item.Id;
    return this.afStore.collection(this.COLLECTION)
      .doc(Id)
      .update(_item)
  }

  public Delete(_id?: string): boolean | Skill | Promise<any> {

    return this.afStore.collection(this.COLLECTION)
      .doc(_id)
      .update({
        Status: Status.DELETED
      })
  }

  public Add(_item: Skill): string | boolean | Skill | Promise<any> {
    return this.afStore.collection(this.COLLECTION)
      .add(_item);
  }

  public DeleteRange(_id: string[]): boolean | Skill | Skill[] | Promise<any> {
    _id.forEach(id => {
      this.Delete(id);
    });
    return true;
  }

}
