import { Component, OnInit } from '@angular/core';
import { IProjectService } from 'src/app/Services/Project/IProjectService';

@Component({
  selector: 'app-featured-work',
  templateUrl: './featured-work.component.html',
  styleUrls: ['./featured-work.component.less']
})
export class FeaturedWorkComponent implements OnInit {

  private projects = [];

  constructor(private projectService: IProjectService) {
    this.registerProjects();
  }

  private registerProjects(): void {
    /* Registering the Data from projectService*/
    this.projectService.Projects.subscribe(response => {
      console.log(response)
      this.projects = response;
    })
  }

  ngOnInit() {
  }

}


