import { Injectable } from '@angular/core';
import { IProjectService } from './IProjectService';
import { Project } from 'src/Classes/Project';
import { AngularFirestore } from '@angular/fire/firestore';
import { environment } from 'src/environments/environment.prod';
import { BehaviorSubject } from 'rxjs';
import { Status } from 'src/Classes/Status';

@Injectable({
  providedIn: 'root'
})
export class ProjectFirebaseService extends IProjectService {

  private projects = new BehaviorSubject([]);

  COLLECTION = environment.COLLECTIONS.PROJECT;

  constructor(private afStore: AngularFirestore) {
    super();
    this.registerProjects();
  }

  private registerProjects(): void {
    this.afStore.collection(this.COLLECTION)
      .ref.where('Status', '==', Status.AVAILABLE)
      .get().then(value => {
        this.projects.next(value.docs.map(proj => {
          let tmp = proj.data();
          tmp.id = proj.id;
          return tmp;
        }))
      })
  }

  public get Projects(): any {
    return this.projects.asObservable();
  }

  public Get(id?: string): Project | Project[] {
    if (id == null || id === undefined) {
      return this.projects.value;
    } else {
      return this.projects.value.filter(project => project.Id == id)[0];
    }
  }

  public Update(item: Project): boolean | Project | Promise<any> {
    const id = item.Id;
    delete item.Id;
    return this.afStore.collection(this.COLLECTION)
      .doc(id)
      .update(item)
  }

  public Delete(id?: string): boolean | Project | Promise<any> {
    if (id == null || id === undefined)
      return false;
    return this.afStore.collection(this.COLLECTION)
      .doc(id)
      .update({
        Status: Status.DELETED
      });
  }

  public Add(item: Project): string | boolean | Project | Promise<any> {
    return this.afStore.collection(this.COLLECTION)
      .add(item);
  }

  public DeleteRange(id: string[]): boolean | Project | Project[] | Promise<any> {
    id.forEach(el => {
      this.Delete(el);
    })
    return true;
  }


}
