import { ICRUD } from 'src/Interfaces/ICRUD';
import { Skill } from 'src/Classes/Skill';

export abstract class ISkillService implements ICRUD<Skill, string>{

    public abstract get Skills();

    public abstract Get(id?: string): Skill | Skill[];
    public abstract Update(item: Skill): boolean | Skill | Promise<any>;
    public abstract Delete(id?: string): boolean | Skill | Promise<any>;
    public abstract Add(item: Skill): string | boolean | Skill | Promise<any>;
    public abstract DeleteRange(id: string[]): boolean | Skill | Skill[] | Promise<any>;

}