import { ICRUD } from 'src/Interfaces/ICRUD';
import { Project } from 'src/Classes/Project';

export abstract class IProjectService implements ICRUD<Project, string>{

    public abstract get Projects();

    public abstract Get(id?: string): Project | Project[];
    public abstract Update(item: Project): boolean | Project | Promise<any>;
    public abstract Delete(id?: string): boolean | Project | Promise<any>;
    public abstract Add(item: Project): string | boolean | Project | Promise<any>;
    public abstract DeleteRange(id: string[]): boolean | Project | Project[] | Promise<any>;

}